.img-banner {
    height: 200px;
    background-image: url(../img/5.jpg);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    @media ( min-width: 768px ) {
        height: 300px; } }
.times {
    li {
        list-style: none; } }
hr {
	width: 100%;
	height: 8px;
	background: linear-gradient(to right, #deac33 25%, #8facce 25%, #8facce 50%, #903487 50%, #903487 75%, #3e6d81 75%);
	margin: 0;
	padding: 0;
	border: 0;
	clear: both; }

#contact {
    @media ( max-width: 768px ) {
        margin-bottom: 30px; } }
