body {
    padding: 0;
    margin: 0;
    color: #000000;
    background-color: lighten(grey,30%);
    font-family: $mainFont!important;
    font-size: 1.5rem;
    &::before {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        content: '';
        height: 10px;
        margin-top: -10px;
        width: 100vw;
        margin-left: 0;
        margin-right: 0;
        box-shadow: 0 0 15px 3px #000;
        z-index: 999; } }
.d-shadow {
    box-shadow: 0 20px 6px -15px #000; }
